<template>
  <v-container
    fill-height
    fluid
  >
    <v-row
      dense
      align="center"
      justify="center"
      v-if="step === 1"
    >

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="12" sm="10">
      <h2> Flight Planner </h2>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <FlightPicker />

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5"></v-col>
      <v-col cols="6" sm="5">
        <v-btn
          block
          color="primary"
          :disabled="!this.$store.state.selectedAircraft"
          v-on:click="next"
          large
        >Next</v-btn>
      </v-col>
      <v-col cols="0" sm="1"></v-col>
    </v-row>

    <v-row
        dense
        align="center"
        justify="center"
        v-if="step === 2"
      >

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="12" sm="10">
        <h2> Flight Planner </h2>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <AirportData />

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <v-btn
          block
          color="primary"
          v-on:click="previous"
          large
        >Previous</v-btn>
      </v-col>
      <v-col cols="6" sm="5">
        <v-btn
          block
          color="primary"
          disabled
          v-on:click="next"
          large
        >Next</v-btn>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

    </v-row>
  </v-container>

</template>

<script>

import FlightPicker from '../components/FlightPicker.vue'
import AirportData from '../components/AirportData.vue'
import axios from 'axios'
axios.defaults.withCredentials = false

export default {
  name: 'Plan',

  components: {
    FlightPicker,
    AirportData
  },

  data: () => ({
    step: 1
  }),

  methods: {

    next: function () {
      this.step++
    },

    previous: function () {
      this.step--
    }

  }

}
</script>
