<template>
   <v-row
    dense
    align="center"
    justify="center"
  >
    <v-col cols="0" sm="1"></v-col>
    <v-col cols="6" sm="5">
      <h3> Takeoff </h3>
    </v-col>
    <v-col cols="6" sm="5">
      <h3> Landing </h3>
    </v-col>
    <v-col cols="0" sm="1"></v-col>

    <v-col cols="0" sm="1"></v-col>
    <v-col cols="6" sm="5">
      <v-text-field
        v-model="takeoffTemperature"
        outlined
        :label="temperatureLabel()"
        prepend-inner-icon="mdi-thermometer"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="6" sm="5">
      <v-text-field
        v-model="landingTemperature"
        outlined
        :label="temperatureLabel()"
        prepend-inner-icon="mdi-thermometer"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="0" sm="1"></v-col>

    <v-col cols="0" sm="1"></v-col>
    <v-col cols="6" sm="5">
      <v-text-field
        v-model="takeoffElevation"
        outlined
        :label="elevationLabel()"
        prepend-inner-icon="mdi-elevation-rise"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="6" sm="5">
      <v-text-field
        v-model="landingElevation"
        outlined
        :label="elevationLabel()"
        prepend-inner-icon="mdi-elevation-rise"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="0" sm="1"></v-col>

    <v-col cols="0" sm="1"></v-col>
    <v-col cols="6" sm="5">
      <v-text-field
        v-model="takeoffWindSpeed"
        outlined
        label="Wind Speed (kt)"
        prepend-inner-icon="mdi-weather-windy"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="6" sm="5">
      <v-text-field
        v-model="landingWindSpeed"
        outlined
        label="Wind Speed (kt)"
        prepend-inner-icon="mdi-weather-windy"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="0" sm="1"></v-col>

    <v-col cols="0" sm="1"></v-col>
    <v-col cols="6" sm="5">
      <v-text-field
        v-model="takeoffWindDirection"
        outlined
        label="Wind Direction (degrees)"
        prepend-inner-icon="mdi-compass-outline"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="6" sm="5">
      <v-text-field
        v-model="landingWindDirection"
        outlined
        label="Wind Direction (degrees)"
        prepend-inner-icon="mdi-compass-outline"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="0" sm="1"></v-col>

    <v-col cols="0" sm="1"></v-col>
    <v-col cols="6" sm="5">
      <v-text-field
        v-model="takeoffLocalPressure"
        outlined
        label="Local Pressure (in. Hg)"
        prepend-inner-icon="mdi-altimeter"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="6" sm="5">
      <v-text-field
        v-model="landingLocalPressure"
        outlined
        label="Local Pressure (in. Hg)"
        prepend-inner-icon="mdi-altimeter"
        hide-details
        type="number"
      />
    </v-col>
    <v-col cols="0" sm="1"></v-col>
  </v-row>
</template>

<script>

export default {

  name: 'AirportData',

  methods: {

    temperatureLabel: function () {
      return 'Temperature (' + this.$store.state.temperatureUnits + ')'
    },

    elevationLabel: function () {
      return 'Elevation (' + this.$store.state.elevationUnits + ')'
    }

  },

  computed: {

    takeoffTemperature: {
      get () {
        return this.$store.getters.takeoffTemperature
      },
      set (value) {
        this.$store.commit('updateTakeoffData', {
          temperature: value
        })
      }
    },

    landingTemperature: {
      get () {
        return this.$store.getters.landingTemperature
      },
      set (value) {
        this.$store.commit('updateLandingData', {
          temperature: value
        })
      }
    },

    takeoffElevation: {
      get () {
        return this.$store.getters.takeoffElevation
      },
      set (value) {
        this.$store.commit('updateTakeoffData', {
          elevation: value
        })
      }
    },

    landingElevation: {
      get () {
        return this.$store.getters.landingElevation
      },
      set (value) {
        this.$store.commit('updateLandingData', {
          elevation: value
        })
      }
    },

    takeoffWindSpeed: {
      get () {
        return this.$store.state.takeoffData.windSpeed
      },
      set (value) {
        this.$store.commit('updateTakeoffData', {
          windSpeed: value
        })
      }
    },

    landingWindSpeed: {
      get () {
        return this.$store.state.landingData.windSpeed
      },
      set (value) {
        this.$store.commit('updateLandingData', {
          windSpeed: value
        })
      }
    },

    takeoffWindDirection: {
      get () {
        return this.$store.state.takeoffData.windDirection
      },
      set (value) {
        this.$store.commit('updateTakeoffData', {
          windDirection: value
        })
      }
    },

    landingWindDirection: {
      get () {
        return this.$store.state.landingData.windDirection
      },
      set (value) {
        this.$store.commit('updateLandingData', {
          windDirection: value
        })
      }
    },

    takeoffLocalPressure: {
      get () {
        return this.$store.state.takeoffData.localPressure
      },
      set (value) {
        this.$store.commit('updateTakeoffData', {
          localPressure: value
        })
      }
    },

    landingLocalPressure: {
      get () {
        return this.$store.state.landingData.localPressure
      },
      set (value) {
        this.$store.commit('updateLandingData', {
          localPressure: value
        })
      }
    }

  }

}

</script>

<style scoped>
  .col-sm-1 {
    padding: 0;
  }
</style>
