import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({

  state: {
    aircraft: [],
    temperatureUnits: 'Farenheit',
    elevationUnits: 'Feet',
    takeoffAirport: undefined,
    takeoffData: {
      autofilled: false,
      observation_time: undefined,
      airportCode: undefined,
      elevation: undefined,
      temperature: undefined,
      windSpeed: undefined,
      windDirection: undefined,
      localPressure: undefined
    },
    landingAirport: undefined,
    landingData: {
      autofilled: false,
      observation_time: undefined,
      airportCode: undefined,
      elevation: undefined,
      temperature: undefined,
      windSpeed: undefined,
      windDirection: undefined,
      localPressure: undefined
    },
    selectedAircraft: undefined
  },

  getters: {

    aircraftNames: (state, getters) => {
      return state.aircraft.map(aircraft => aircraft.name)
    },

    aircraftFromName: (state, getters) => (name) => {
      let aircraft
      for (let i; i < state.aircraft.length; i++) {
        aircraft = state.aircraft[i]
        if (aircraft.name === name) {
          return aircraft
        }
      }
    },

    takeoffTemperature: (state, getters) => {
      if (state.takeoffData.temperature === undefined) {
        return state.takeoffData.temperature
      }
      if (state.temperatureUnits === 'Celsius') {
        return state.takeoffData.temperature
      } else if (state.temperatureUnits === 'Farenheit') {
        return (state.takeoffData.temperature * 9 / 5 + 32).toFixed(2)
      }
    },

    landingTemperature: (state, getters) => {
      if (state.landingData.temperature === undefined) {
        return state.landingData.temperature
      }
      if (state.temperatureUnits === 'Celsius') {
        return state.landingData.temperature
      } else {
        return (state.landingData.temperature * 9 / 5 + 32).toFixed(2)
      }
    },

    takeoffElevation: (state, getters) => {
      if (state.takeoffData.elevation === undefined) {
        return state.takeoffData.elevation
      }
      if (state.elevationUnits === 'Meters') {
        return state.takeoffData.elevation
      } else if (state.elevationUnits === 'Feet') {
        return (state.takeoffData.elevation * 3.28084).toFixed(2)
      }
    },

    landingElevation: (state, getters) => {
      if (state.landingData.elevation === undefined) {
        return state.landingData.elevation
      }
      if (state.temperatureUnits === 'Celsius') {
        return state.landingData.elevation
      } else {
        return (state.landingData.elevation * 3.28084).toFixed(2)
      }
    }

  },

  mutations: {

    setAircraft (state, payload) {
      state.aircraft = payload
    },

    selectAircraft (state, payload) {
      state.selectedAircraft = payload
    },

    setTakeoffAirport (state, payload) {
      state.takeoffAirport = payload
    },

    setTakeoffData (state, payload) {
      state.takeoffData = payload
    },

    updateTakeoffData (state, payload) {
      state.takeoffData = { ...state.takeoffData, ...payload }
    },

    setLandingAirport (state, payload) {
      state.landingAirport = payload
    },

    setLandingData (state, payload) {
      state.landingData = payload
    },

    updateLanding (state, payload) {
      state.landingData = { ...state.landingData, ...payload }
    },

    resetTakeoffData (state) {
      state.takeoffData = {
        autofilled: false,
        observation_time: undefined,
        airportCode: undefined,
        elevation: undefined,
        temperature: undefined,
        windSpeed: undefined,
        windDirection: undefined,
        localPressure: undefined
      }
    },

    resetLandingData (state) {
      state.landingData = {
        autofilled: false,
        observation_time: undefined,
        airportCode: undefined,
        elevation: undefined,
        temperature: undefined,
        windSpeed: undefined,
        windDirection: undefined,
        localPressure: undefined
      }
    },

    setTemperatureUnits (state, payload) {
      state.temperatureUnits = payload
    }
  },

  actions: {
  },

  modules: {
  }
})
