<template>
  <v-row
    align="center"
    justify="center"
  >

    <v-col cols="0" sm="1"></v-col>
    <v-col cols="12" sm="10">
    <v-select
      outlined
      hide-details
      :items="this.$store.getters.aircraftNames"
      label="Which aircraft?"
      prepend-inner-icon="mdi-airplane"
      v-model="selectedAircraft"
    ></v-select>
    </v-col>
    <v-col cols="0" sm="1"></v-col>

    <v-col cols="0" sm="1"></v-col>
    <v-col cols="6" sm="5">
    <v-autocomplete
      outlined
      :items="airports"
      label="Where from?"
      clearable
      hide-details
      prepend-inner-icon="mdi-map-marker"
      v-model="takeoffAirport"
    ></v-autocomplete>
    </v-col>
    <v-col cols="6" sm="5">
    <v-autocomplete
      outlined
      :items="airports"
      label="Where to?"
      clearable
      hide-details
      prepend-inner-icon="mdi-map-marker"
      v-model="landingAirport"
    ></v-autocomplete>
    </v-col>
    <v-col cols="0" sm="1"></v-col>

    <v-col cols="0" sm="1"></v-col>
    <v-col cols="6" sm="5">
    <v-menu
      v-model="dateMenu"
      :close-on-content-click="false"
      :nudge-right="40"
      transition="scale-transition"
      offset-y
      min-width="290px"
    >
      <template v-slot:activator="{ on, attrs }">
      <v-text-field
        hide-details
        v-model="date"
        label="What day?"
        prepend-inner-icon="mdi-calendar"
        readonly
        outlined
        v-bind="attrs"
        v-on="on"
      ></v-text-field>
      </template>
      <v-date-picker
      v-model="date"
      @input="dateMenu = false">
      </v-date-picker>
    </v-menu>
    </v-col>
    <v-col cols="6" sm="5">
      <v-menu
        ref="menu"
        v-model="timeMenu"
        :close-on-content-click="false"
        :nudge-right="40"
        :return-value.sync="time"
        transition="scale-transition"
        offset-y
        max-width="290px"
        min-width="290px"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
              v-model="time"
              outlined
              hide-details
              label="What time?"
              prepend-inner-icon="mdi-clock-outline"
              readonly
              v-bind="attrs"
              v-on="on"
          ></v-text-field>
        </template>
        <v-time-picker
          v-if="timeMenu"
          v-model="time"
          full-width
          @click:minute="$refs.menu.save(time)"
        ></v-time-picker>
      </v-menu>
    </v-col>
    <v-col cols="0" sm="1"></v-col>
  </v-row>
</template>

<script>

export default {
  name: 'FlightPicker',

  data: () => ({
    dateMenu: false,
    timeMenu: false,
    date: undefined,
    time: undefined,
    airports: []
  }),

  created: function () {
    this.loadAirportCodes()
  },

  computed: {

    selectedAircraft: {
      get () {
        return this.$store.state.selectedAircraft
      },
      set (value) {
        this.$store.commit('selectAircraft', value)
      }
    },

    takeoffAirport: {
      get () {
        return this.$store.state.takeoffAirport
      },
      set (value) {
        this.$store.commit('setTakeoffAirport', value)
        this.attemptToPopulateData('takeoff')
      }
    },

    landingAirport: {
      get () {
        return this.$store.state.landingAirport
      },
      set (value) {
        this.$store.commit('setLandingAirport', value)
        this.attemptToPopulateData('landing')
      }
    }

  },

  methods: {

    parseAirportCodes: function (data) {
      const codes = []
      const xml = data.getElementsByTagName('Station')
      for (let i = 0; i < xml.length; i++) {
        const stationXml = xml[i]
        const stationId = stationXml.getElementsByTagName('station_id')[0].innerHTML
        const stationSite = stationXml.getElementsByTagName('site')[0].innerHTML
        const stationState = stationXml.getElementsByTagName('state')[0].innerHTML
        codes.push({
          value: stationId,
          text: stationSite + ', ' + stationState + ' (' + stationId + ')'
        })
      }
      return codes
    },

    loadAirportCodes: function () {
      fetch('https://cors-anywhere.herokuapp.com/https://www.aviationweather.gov/adds/dataserver_current/httpparam?dataSource=stations&requestType=retrieve&format=xml&stationString=~us', {
        method: 'GET',
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Content-Type': 'text/xml'
        }
      })
        .then(response => response.text())
        .then(str => (new window.DOMParser()).parseFromString(str, 'text/xml'))
        .then(data => {
          const response = this.parseAirportCodes(data)
          this.airports = response
        })
    },

    parseMetarXml: function (data) {
      const json = {}
      const xml = data.getElementsByTagName('METAR')[0]
      if (!xml) {
        return json
      }
      json.autofilled = true
      json.latitude = Number(xml.getElementsByTagName('latitude')[0].innerHTML)
      json.longitude = Number(xml.getElementsByTagName('longitude')[0].innerHTML)
      json.temperature = Number(xml.getElementsByTagName('temp_c')[0].innerHTML)
      json.windSpeed = Number(xml.getElementsByTagName('wind_speed_kt')[0].innerHTML)
      json.elevation = Number(xml.getElementsByTagName('elevation_m')[0].innerHTML)
      json.windDirection = Number(xml.getElementsByTagName('wind_dir_degrees')[0].innerHTML)
      json.observationTime = new Date(xml.getElementsByTagName('observation_time')[0].innerHTML)
      json.localPressure = Number(xml.getElementsByTagName('altim_in_hg')[0].innerHTML)
      return json
    },

    attemptToPopulateData: function (dataset) {
      let airportCode
      let commitMethod
      if (dataset === 'takeoff') {
        this.$store.commit('resetTakeoffData')
        airportCode = this.$store.state.takeoffAirport
        commitMethod = 'setTakeoffData'
      } else if (dataset === 'landing') {
        this.$store.commit('resetLandingData')
        airportCode = this.$store.state.landingAirport
        commitMethod = 'setLandingData'
      } else {
        return
      }
      fetch('https://cors-anywhere.herokuapp.com/https://www.aviationweather.gov/adds/dataserver_current/httpparam?dataSource=metars&requestType=retrieve&format=xml&stationString=' + airportCode + '&hoursBeforeNow=12&mostRecent=true', {
        method: 'GET',
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Content-Type': 'text/xml'
        }
      })
        .then(response => response.text())
        .then(str => (new window.DOMParser()).parseFromString(str, 'text/xml'))
        .then(data => {
          if (data !== undefined) {
            const response = this.parseMetarXml(data)
            if (Object.keys(response).length > 0) {
              this.$store.commit(commitMethod, response)
            }
          }
        })
    }

  }

}

</script>

<style scoped>
  .col-sm-1 {
    padding: 0;
  }
</style>
